// BASIC
import React, { Component } from 'react'
import styled, {keyframes, css} from 'styled-components'
// IMAGES
import loader from '../Images/loader.svg'

const waitAnimation = keyframes`
	from {
		transform: translate(50%, 50%) scale(1) rotate(0);
	}
	16.66% {
		transform: translate(50%, 50%) scale(0.8) rotate(60deg);
	}
	33.33% {
		transform: translate(50%, 50%) scale(1) rotate(120deg);
	}
	50% {
		transform: translate(50%, 50%) scale(0.8) rotate(180deg);
	}
	66.66% {
		transform: translate(50%, 50%) scale(1) rotate(240deg);
	}
	83.33% {
		transform: translate(50%, 50%) scale(0.8) rotate(300deg);
	}
	100% {
		transform: translate(50%, 50%) scale(1) rotate(360deg);
	}
`
const LoaderComponent = styled.img`
	position: absolute;
	bottom: 50%;
	right: 50%;
	transform: translate(50%, 50%);
	width: 150px;
	height: 150px;
	transition: 0.3s ease;
	animation: ${waitAnimation} 3s linear infinite;
	${props =>
		props.hide &&
		css`
			opacity: 0;
		`
	};
`

class Loader extends Component {
	sleep = (ms) => {
		return new Promise(resolve => setTimeout(resolve, ms));
	}
	componentWillUnmount() {
		// this.sleep(2000).then(() => { console.log("World!"); });
		// setTimeout(() => {
		// 	console.log("World!");
		// }, 2000)
	}
	render() {
		return (
			<LoaderComponent onClick={this.hide} hide={this.props.hide} src={loader} />
		);
	}
}

export default Loader